@import url('https://fonts.googleapis.com/css2?family=Noto+Sans:wght@500&display=swap');

.top{
    margin-top: 100px;
}

.top h2{
    margin-top: 60px;
    margin-bottom: 10px;
    font-size: 29px;
    font-weight: bold;
    text-align: center;
    font-family: 'Noto Sans', sans-serif;
}
.top hr{
    display: block;
    padding: 0;
    margin: 0;
    margin: auto;
    margin-bottom: 45px;
    width:18%;
    text-align: center;
    height:5px;
    /* border-width: 10; */
    border-width: 1px;
    background: linear-gradient(to right, #feac5e, #c779d0, #4bc0c8);
    border-radius: 1px;
    border:0;
}

.contactMain{
    display: flex;
    width: 70%;
    margin: auto;
}