@import url('https://fonts.googleapis.com/css2?family=Noto+Sans:wght@500&display=swap');

.top{
    margin-top: 120px;
    /* height:400px */
}

.top h2{
    margin-top: 60px;
    margin-bottom: 10px;
    font-size: 29px;
    font-weight: bold;
    text-align: center;
    font-family: 'Noto Sans', sans-serif;
}
.top hr{
    display: block;
    padding: 0;
    margin: 0;
    margin: auto;
    margin-bottom: 45px;
    width:18%;
    text-align: center;
    height:5px;
    /* border-width: 10; */
    border-width: 1px;
    background: linear-gradient(to right, #feac5e, #c779d0, #4bc0c8);
    border-radius: 1px;
    border:0;
}

.skillcard{
    width: 80%;
    margin: auto;
    margin-top: 100px;
    display: grid;
    grid-template-columns: repeat(7,1fr);
    gap:35px;
}
.singleCard{
    /* border: 1px solid chartreuse; */
    padding: 10px 8px 10px 8px;
    border-radius: 14px;
    background-color: rgb(39 45 57);
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.singleCard>img{
    width: 100%;
    height:130px;
}

.singleCard>p{
    text-align: center;
    font-size: 20px;
    /* font-weight: 100; */
    margin-top: 10px;
    font-family: 'Noto Sans', sans-serif;
}