@import url('https://fonts.googleapis.com/css2?family=Noto+Sans:wght@500&display=swap');


.top{
    margin-top: 100px;
}

.top h2{
    margin-top: 60px;
    margin-bottom: 10px;
    font-size: 29px;
    font-weight: bold;
    text-align: center;
    font-family: 'Noto Sans', sans-serif;
}
.top hr{
    display: block;
    padding: 0;
    margin: 0;
    margin: auto;
    margin-bottom: 45px;
    width:18%;
    text-align: center;
    height:5px;
    /* border-width: 10; */
    border-width: 1px;
    background: linear-gradient(to right, #feac5e, #c779d0, #4bc0c8);
    border-radius: 1px;
    border:0;
}

.projectMain{
    width: 80%;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    gap: 10px;
}

.projectCard{
    border: 1px solid chartreuse;
   
    border-radius: 16px;
}

.projectCard img{
    display: none;
}

.projectCard>p:nth-child(3){
    text-align: center;
    font-weight: bold;
    font-size: 20px;
    font-family: 'Noto Sans', sans-serif;
}
.projectCard>p:nth-child(4){
    margin-left: 16px;
    margin-right: 16px;
}
.projectCard>p:nth-child(5),.projectCard>p:nth-child(6),.projectCard>p:nth-child(7){
    margin-left: 16px;
    margin-right: 16px;
    font-weight: bold;

}

